@import "variables";
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


body {
    background-color: $backgroundColor;
    color: $color;
    font-family: $fontFamily;
}

header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    
    .header-logo {
        display: flex;
        align-items: center;
        
    }

    .header-buttons {
        display: flex;
        div {
            margin: 15px;
            a {
                text-decoration: none;
                color: $color;
            }
            .button {
                padding: 15px;
                font-size: large;
                &:hover {
                    background-color: white;
                    color: black;
                }  
            }   
        }
        
    }

    .platform-title {
        margin-left: 0.7rem;
        font-size: 2rem;
    }
}

.section-title {
    margin-top: 40px;
    margin-left: 15px;
    font-size: 1.3rem;
    font-family: $fontFamily;
    
  }


  .top {
    display: flex;
    flex-direction: row-reverse;
  }